const app = getApp().globalData;
const { navBar } = app;
export default {
	data() {
		return {
			kind: 0,
			navBar: navBar
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		selectStage(index) {
			this.kind = index;
		}
	}
};
